import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { WarehouseSettingsComponent } from './warehouse-settings.component';
import { WarehouseProfileComponent } from './components/warehouse-profile/warehouse-profile.component';

const routes: Routes = [
  {
    path:'',
    component: WarehouseSettingsComponent,
    children: [
      {
        path:'',
        redirectTo: 'warehouse-profile-setting',
        pathMatch: 'full'
      },
      {
        path:'warehouse-profile-setting',
        component: WarehouseSettingsComponent,
      }, 
      // {
      //   path:'warehouse-profile',
      //   component: WarehouseProfileComponent,
      // }, 
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WarehouseSettingsRoutingModule { }
